/* ========================================
   NAVBAR - DARK 3D DESIGN (FOOTER STYLE)
   ======================================== */

/* Main Navbar Header */
.navbar-3d-header {
  position: sticky;
  top: 1rem;
  z-index: 100;
  width: 95%;
  max-width: 1400px;
  margin: 0 auto;
  border-radius: 20px;
  background: linear-gradient(135deg, rgba(15, 23, 42, 0.95) 0%, rgba(30, 27, 75, 0.95) 50%, rgba(15, 23, 42, 0.95) 100%);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(139, 92, 246, 0.2);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5),
              0 0 20px rgba(139, 92, 246, 0.15),
              inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transition: all 0.4s ease;
  transform-style: preserve-3d;
  position: relative;
  overflow: hidden;
}

.navbar-3d-header.scrolled {
  background: linear-gradient(135deg, rgba(15, 23, 42, 0.98) 0%, rgba(30, 27, 75, 0.98) 50%, rgba(15, 23, 42, 0.98) 100%);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.6),
              0 0 30px rgba(139, 92, 246, 0.25);
}

.navbar-3d-header:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.6),
              0 0 30px rgba(139, 92, 246, 0.3);
}

/* ========================================
   ANIMATED BACKGROUND
   ======================================== */
.navbar-bg-animation {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: 0;
  opacity: 0.2;
  pointer-events: none;
}

.navbar-wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 200%;
  height: 100%;
  background: linear-gradient(180deg, transparent, rgba(139, 92, 246, 0.1));
  animation: navWaveAnimation 15s ease-in-out infinite;
}

.navbar-wave-1 {
  animation-delay: 0s;
}

.navbar-wave-2 {
  animation-delay: -5s;
  opacity: 0.5;
  animation-duration: 20s;
}

/* ========================================
   FLOATING PARTICLES
   ======================================== */
.navbar-particles {
  position: absolute;
  inset: 0;
  overflow: hidden;
  z-index: 1;
  pointer-events: none;
}

.nav-particle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: rgba(139, 92, 246, 0.6);
  border-radius: 50%;
  animation: navFloatParticle 12s ease-in-out infinite;
}

.nav-particle-1 {
  top: 20%;
  left: 20%;
  animation-delay: 0s;
}

.nav-particle-2 {
  top: 60%;
  right: 30%;
  animation-delay: -4s;
  animation-duration: 15s;
}

.nav-particle-3 {
  top: 40%;
  left: 60%;
  animation-delay: -8s;
  animation-duration: 18s;
}

/* ========================================
   CONTAINER
   ======================================== */
.navbar-3d-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  flex-wrap: wrap;
  position: relative;
  z-index: 2;
}

/* ========================================
   LOGO SECTION - FIXED
   ======================================== */
.navbar-left-3d {
  position: relative;
  z-index: 10;
}

.navbar-logo-3d {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.4s ease;
}

.logo-glow {
  position: absolute;
  inset: -10px;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.15) 0%, transparent 70%);
  border-radius: 12px;
  filter: blur(15px);
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: 0;
}

.navbar-logo-3d:hover .logo-glow {
  opacity: 1;
}

.navbar-logo-3d:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.navbar-logo-3d img {
  width: 90px;
  height: auto;
  max-height: 90px;
  display: block;
  object-fit: contain;
  filter: brightness(1.2) contrast(1.1);
  transition: transform 0.4s ease, filter 0.3s ease;
  position: relative;
  z-index: 1;
}

.navbar-logo-3d:hover img {
  transform: scale(1.05);
  filter: brightness(1.3) contrast(1.2) drop-shadow(0 0 20px rgba(255, 255, 255, 0.3));
}

/* ========================================
   HAMBURGER BUTTON
   ======================================== */
.navbar-toggle-3d {
  display: none;
  flex-direction: column;
  gap: 5px;
  background: rgba(139, 92, 246, 0.2);
  border: 1px solid rgba(139, 92, 246, 0.3);
  border-radius: 12px;
  padding: 10px;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  z-index: 11;
}

.navbar-toggle-3d:hover {
  background: rgba(139, 92, 246, 0.3);
  box-shadow: 0 5px 15px rgba(139, 92, 246, 0.3);
}

.bar-3d {
  width: 24px;
  height: 3px;
  background: linear-gradient(90deg, #8b5cf6, #ec4899);
  border-radius: 3px;
  transition: all 0.3s ease;
}

.navbar-toggle-3d.active .bar-3d:nth-child(1) {
  transform: rotate(45deg) translate(8px, 8px);
}

.navbar-toggle-3d.active .bar-3d:nth-child(2) {
  opacity: 0;
}

.navbar-toggle-3d.active .bar-3d:nth-child(3) {
  transform: rotate(-45deg) translate(8px, -8px);
}

/* ========================================
   MENU WRAPPER
   ======================================== */
.navbar-menu-wrapper-3d {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.navbar-menu-3d {
  display: flex;
  list-style: none;
  gap: 0.5rem;
  margin: 0;
  padding: 0;
}

/* ========================================
   NAVIGATION LINKS - 3D
   ======================================== */
.navbar-menu-3d li {
  position: relative;
}

.nav-link-3d {
  position: relative;
  display: flex;
  align-items: center;
  gap: 8px;
  text-decoration: none;
  color: #cbd5e1;
  font-weight: 600;
  font-size: 1rem;
  padding: 12px 20px;
  border-radius: 12px;
  background: rgba(30, 41, 59, 0.6);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(148, 163, 184, 0.2);
  transition: all 0.35s ease;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  perspective: 1000px;
  transform-style: preserve-3d;
}

/* Link Glow Effect */
.link-glow {
  position: absolute;
  inset: -5px;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.3), rgba(236, 72, 153, 0.3));
  border-radius: 12px;
  filter: blur(15px);
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: -1;
}

.nav-link-3d.hovered .link-glow,
.nav-link-3d:hover .link-glow {
  opacity: 1;
}

.nav-link-3d:hover {
  background: rgba(30, 41, 59, 0.9);
  color: #fff;
  transform: translateY(-4px) rotateX(5deg);
  border-color: rgba(139, 92, 246, 0.5);
  box-shadow: 0 10px 25px rgba(139, 92, 246, 0.3);
}

/* Active State */
.nav-link-3d.active {
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.3), rgba(236, 72, 153, 0.3));
  color: #fff;
  border-color: rgba(139, 92, 246, 0.6);
  box-shadow: 0 8px 20px rgba(139, 92, 246, 0.4);
}

/* Icon Styling */
.nav-icon-3d {
  font-size: 18px;
  transition: transform 0.3s ease;
}

.nav-link-3d:hover .nav-icon-3d {
  transform: scale(1.2) rotate(5deg);
}

/* ========================================
   RESUME BUTTON - 3D
   ======================================== */
.navbar-right-3d {
  position: relative;
}

.resume-btn-3d {
  position: relative;
  background: linear-gradient(135deg, #8b5cf6, #ec4899);
  color: #fff;
  padding: 12px 28px;
  font-size: 0.95rem;
  font-weight: 700;
  border-radius: 50px;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  border: 2px solid rgba(139, 92, 246, 0.5);
  transition: all 0.35s ease;
  box-shadow: 0 8px 20px rgba(139, 92, 246, 0.4);
  overflow: hidden;
  cursor: pointer;
}

/* Resume Glow */
.resume-glow {
  position: absolute;
  inset: -5px;
  background: linear-gradient(135deg, rgba(139, 92, 246, 0.5), rgba(236, 72, 153, 0.5));
  border-radius: 50px;
  filter: blur(15px);
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: -1;
}

.resume-btn-3d:hover .resume-glow {
  opacity: 1;
}

.resume-btn-3d::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  transform: translate(-50%, -50%);
  transition: width 0.5s, height 0.5s;
}

.resume-btn-3d:hover::before {
  width: 300px;
  height: 300px;
}

.resume-btn-3d:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 15px 35px rgba(139, 92, 246, 0.6);
  border-color: #fff;
}

.resume-icon {
  font-size: 1.2rem;
  animation: bounceIcon 2s infinite;
}

/* ========================================
   ANIMATIONS
   ======================================== */
@keyframes navWaveAnimation {
  0%, 100% {
    transform: translateX(0) translateY(0);
  }
  50% {
    transform: translateX(-25%) translateY(-8px);
  }
}

@keyframes navFloatParticle {
  0%, 100% {
    transform: translateY(0) translateX(0);
    opacity: 0.6;
  }
  50% {
    transform: translateY(-15px) translateX(10px);
    opacity: 1;
  }
}

@keyframes bounceIcon {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-4px);
  }
}

.bounce {
  animation: bounceEffect 0.6s ease;
}

@keyframes bounceEffect {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

/* ========================================
   RESPONSIVE DESIGN
   ======================================== */

/* Tablet and below */
@media (max-width: 768px) {
  .navbar-toggle-3d {
    display: flex;
  }

  .navbar-menu-wrapper-3d {
    display: none;
    flex-direction: column;
    width: 100%;
    margin-top: 1rem;
    background: linear-gradient(135deg, rgba(15, 23, 42, 0.98) 0%, rgba(30, 27, 75, 0.98) 100%);
    backdrop-filter: blur(20px);
    border-radius: 16px;
    border: 1px solid rgba(139, 92, 246, 0.3);
    padding: 1.5rem;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  }

  .navbar-menu-wrapper-3d.active {
    display: flex;
    animation: slideDown 0.3s ease;
  }

  @keyframes slideDown {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .navbar-menu-3d {
    flex-direction: column;
    width: 100%;
    gap: 0.5rem;
  }

  .nav-link-3d {
    width: 100%;
    justify-content: flex-start;
    padding: 14px 20px;
    color: #fff;
  }

  .resume-btn-3d {
    width: 100%;
    justify-content: center;
    margin-top: 1rem;
  }
  
  .navbar-logo-3d img {
    width: 75px;
    max-height: 75px;
  }
}

/* Small Mobile */
@media (max-width: 480px) {
  .navbar-3d-header {
    width: 92%;
    border-radius: 14px;
    top: 0.5rem;
  }

  .navbar-3d-container {
    padding: 0.8rem 1rem;
  }

  .navbar-logo-3d img {
    width: 65px;
    max-height: 65px;
  }

  .navbar-menu-wrapper-3d {
    padding: 1rem;
  }

  .nav-link-3d {
    font-size: 0.95rem;
    padding: 12px 16px;
  }

  .resume-btn-3d {
    font-size: 0.9rem;
    padding: 12px 20px;
  }
}

/* Extra Small */
@media (max-width: 360px) {
  .navbar-3d-header {
    width: 90%;
  }

  .navbar-logo-3d img {
    width: 55px;
    max-height: 55px;
  }

  .nav-link-3d {
    font-size: 0.9rem;
  }

  .resume-btn-3d {
    font-size: 0.85rem;
  }
}
/* ==================== RESUME POPUP MODAL ==================== */

.resume-popup-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  z-index: 99999;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.resume-popup-container {
  position: relative;
  width: 100%;
  max-width: 900px;
  height: 90vh;
  max-height: 900px;
  background: linear-gradient(135deg, 
    rgba(15, 23, 42, 0.98) 0%, 
    rgba(30, 27, 75, 0.98) 50%,
    rgba(15, 23, 42, 0.98) 100%
  );
  border-radius: 24px;
  border: 2px solid rgba(139, 92, 246, 0.3);
  box-shadow: 
    0 20px 60px rgba(0, 0, 0, 0.5),
    0 0 0 1px rgba(255, 255, 255, 0.1),
    inset 0 1px 0 rgba(255, 255, 255, 0.1),
    0 0 100px rgba(139, 92, 246, 0.2);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: slideUp 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Glowing border animation */
.resume-popup-container::before {
  content: '';
  position: absolute;
  inset: -2px;
  background: linear-gradient(
    45deg,
    transparent 30%,
    rgba(139, 92, 246, 0.5) 50%,
    transparent 70%
  );
  border-radius: 24px;
  opacity: 0;
  animation: borderGlow 3s ease-in-out infinite;
  pointer-events: none;
  z-index: -1;
}

@keyframes borderGlow {
  0%, 100% {
    opacity: 0;
    transform: rotate(0deg);
  }
  50% {
    opacity: 1;
    transform: rotate(180deg);
  }
}

.resume-popup-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 32px;
  border-bottom: 1px solid rgba(139, 92, 246, 0.2);
  background: rgba(30, 41, 59, 0.5);
  position: relative;
  z-index: 1;
}

.resume-popup-header::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(139, 92, 246, 0.5) 50%,
    transparent
  );
}

.resume-popup-header h2 {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  background: linear-gradient(135deg, #667eea, #ec4899);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: 0.5px;
  position: relative;
}

.resume-popup-header h2::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 50px;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #ec4899);
  border-radius: 2px;
}

.resume-popup-actions {
  display: flex;
  align-items: center;
  gap: 12px;
}

.download-btn,
.close-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  background: rgba(30, 41, 59, 0.6);
  color: #cbd5e0;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  position: relative;
  overflow: hidden;
}

.download-btn::before,
.close-btn::before {
  content: '';
  position: absolute;
  inset: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(236, 72, 153, 0.3));
  opacity: 0;
  transition: opacity 0.3s ease;
}

.download-btn:hover::before,
.close-btn:hover::before {
  opacity: 1;
}

.download-btn svg,
.close-btn svg {
  position: relative;
  z-index: 1;
}

.download-btn:hover,
.close-btn:hover {
  color: #ffffff;
  border-color: rgba(139, 92, 246, 0.5);
  transform: scale(1.1) translateY(-2px);
  box-shadow: 0 8px 20px rgba(139, 92, 246, 0.4);
}

.close-btn:hover {
  background: rgba(239, 68, 68, 0.2);
  border-color: rgba(239, 68, 68, 0.5);
  color: #ef4444;
  box-shadow: 0 8px 20px rgba(239, 68, 68, 0.4);
}

.close-btn:hover::before {
  background: rgba(239, 68, 68, 0.3);
}

.resume-popup-body {
  flex: 1;
  padding: 24px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

/* Background gradient effect */
.resume-popup-body::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(
    circle,
    rgba(139, 92, 246, 0.1) 0%,
    transparent 50%
  );
  animation: rotateGradient 10s linear infinite;
  pointer-events: none;
}

@keyframes rotateGradient {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.resume-iframe {
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 16px;
  background: #ffffff;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 0 1px rgba(139, 92, 246, 0.2);
  position: relative;
  z-index: 1;
  transition: transform 0.3s ease;
}

.resume-iframe:hover {
  transform: scale(1.01);
}

/* Loading animation for iframe */
.resume-popup-body::after {
  content: 'Loading Resume...';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgba(255, 255, 255, 0.6);
  font-size: 16px;
  font-weight: 600;
  pointer-events: none;
  z-index: 0;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.4;
  }
  50% {
    opacity: 1;
  }
}

/* Responsive Design for Popup */
@media (max-width: 768px) {
  .resume-popup-overlay {
    padding: 12px;
  }

  .resume-popup-container {
    max-width: 100%;
    height: 85vh;
    border-radius: 20px;
  }

  .resume-popup-header {
    padding: 16px 20px;
  }

  .resume-popup-header h2 {
    font-size: 20px;
  }

  .download-btn,
  .close-btn {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }

  .resume-popup-body {
    padding: 16px;
  }
}

@media (max-width: 480px) {
  .resume-popup-container {
    height: 90vh;
    border-radius: 16px;
  }

  .resume-popup-header {
    padding: 12px 16px;
  }

  .resume-popup-header h2 {
    font-size: 18px;
  }

  .resume-popup-header h2::after {
    width: 35px;
    height: 2px;
  }

  .download-btn,
  .close-btn {
    width: 36px;
    height: 36px;
    font-size: 14px;
  }

  .resume-popup-body {
    padding: 12px;
  }

  .resume-popup-body::after {
    font-size: 14px;
  }
}

/* Smooth scrollbar for mobile */
.resume-popup-container::-webkit-scrollbar {
  width: 6px;
}

.resume-popup-container::-webkit-scrollbar-track {
  background: rgba(30, 41, 59, 0.5);
  border-radius: 3px;
}

.resume-popup-container::-webkit-scrollbar-thumb {
  background: rgba(139, 92, 246, 0.5);
  border-radius: 3px;
}

.resume-popup-container::-webkit-scrollbar-thumb:hover {
  background: rgba(139, 92, 246, 0.7);
}