/* ==================== MODERN FOOTER DESIGN ==================== */

.modern-footer {
  position: relative;
  background: linear-gradient(135deg, #0f0c29 0%, #302b63 50%, #24243e 100%);
  color: #ffffff;
  padding: 80px 0 30px;
  overflow: hidden;
  margin-top: 100px;
}

/* Background Effects */
.footer-bg-effects {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
}

.gradient-blob {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.3;
  animation: blobFloat 20s infinite ease-in-out;
}

.blob-1 {
  width: 400px;
  height: 400px;
  background: linear-gradient(45deg, #667eea, #764ba2);
  top: -100px;
  left: -100px;
  animation-delay: 0s;
}

.blob-2 {
  width: 350px;
  height: 350px;
  background: linear-gradient(45deg, #f093fb, #f5576c);
  bottom: -100px;
  right: -100px;
  animation-delay: 5s;
}

.blob-3 {
  width: 300px;
  height: 300px;
  background: linear-gradient(45deg, #4facfe, #00f2fe);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation-delay: 10s;
}

@keyframes blobFloat {
  0%, 100% { transform: translate(0, 0) scale(1); }
  33% { transform: translate(30px, -30px) scale(1.1); }
  66% { transform: translate(-20px, 20px) scale(0.9); }
}

.grid-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(255, 255, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
  background-size: 50px 50px;
  opacity: 0.3;
}

/* Footer Container */
.footer-container {
  position: relative;
  z-index: 1;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
}

/* Footer Top Grid */
.footer-top {
  display: grid;
  grid-template-columns: 2fr 1fr 1.5fr 1.5fr;
  gap: 60px;
  margin-bottom: 60px;
}

/* Footer Columns */
.footer-column {
  display: flex;
  flex-direction: column;
}

/* Brand Column */
.footer-brand-col {
  max-width: 400px;
}

.footer-logo-link {
  display: inline-block;
  margin-bottom: 20px;
  transition: transform 0.3s ease;
}

.footer-logo-link:hover {
  transform: scale(1.05) rotate(2deg);
}

.footer-logo {
  width: 80px;
  height: 80px;
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(102, 126, 234, 0.3);
  transition: all 0.3s ease;
}

.footer-logo:hover {
  box-shadow: 0 15px 40px rgba(102, 126, 234, 0.5);
}

.footer-brand-name {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 12px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.footer-tagline {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 16px;
  color: #a0aec0;
  margin-bottom: 15px;
  font-weight: 500;
}

.tagline-icon {
  color: #667eea;
  font-size: 18px;
}

.footer-description {
  font-size: 15px;
  line-height: 1.8;
  color: #cbd5e0;
  opacity: 0.9;
}

/* Footer Heading */
.footer-heading {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 25px;
  color: #ffffff;
  position: relative;
  padding-bottom: 12px;
}

.footer-heading::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 40px;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #764ba2);
  border-radius: 2px;
}

/* Quick Links */
.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 12px;
}

.footer-link {
  display: flex;
  align-items: center;
  gap: 10px;
  color: #cbd5e0;
  text-decoration: none;
  font-size: 15px;
  transition: all 0.3s ease;
  padding: 8px 0;
}

.footer-link:hover {
  color: #667eea;
  transform: translateX(5px);
}

.link-arrow {
  opacity: 0;
  transform: translateX(-10px);
  transition: all 0.3s ease;
  color: #667eea;
}

.footer-link:hover .link-arrow {
  opacity: 1;
  transform: translateX(0);
}

/* Social Cards */
.footer-social-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 15px;
}

.footer-social-card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 10px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  text-decoration: none;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
  backdrop-filter: blur(10px);
}

.social-card-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--social-color);
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: 0;
}

.footer-social-card:hover .social-card-bg {
  opacity: 0.15;
}

.footer-social-card.active {
  transform: translateY(-8px);
  border-color: var(--social-color);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.social-card-icon {
  position: relative;
  z-index: 1;
  font-size: 28px;
  color: #ffffff;
  margin-bottom: 8px;
  transition: all 0.3s ease;
}

.footer-social-card:hover .social-card-icon {
  color: var(--social-color);
  transform: scale(1.2);
}

.social-card-name {
  position: relative;
  z-index: 1;
  font-size: 13px;
  color: #cbd5e0;
  font-weight: 500;
  transition: color 0.3s ease;
}

.footer-social-card:hover .social-card-name {
  color: #ffffff;
}

/* CTA Column */
.footer-cta-col {
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  padding: 30px;
  border-radius: 20px;
  border: 1px solid rgba(102, 126, 234, 0.2);
}

.footer-cta-text {
  color: #cbd5e0;
  font-size: 15px;
  line-height: 1.7;
  margin-bottom: 25px;
}

.footer-cta-button {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 14px 28px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #ffffff;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 600;
  font-size: 15px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.footer-cta-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(102, 126, 234, 0.6);
}

.cta-icon {
  font-size: 18px;
}

/* Divider */
.footer-divider {
  position: relative;
  margin: 40px 0;
  height: 1px;
}

.divider-line {
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(255, 255, 255, 0.2) 50%, 
    transparent 100%
  );
}

.divider-dot {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 8px;
  background: linear-gradient(135deg, #667eea, #764ba2);
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(102, 126, 234, 0.6);
}

/* Footer Bottom */
.footer-bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
}

.footer-bottom-center {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  text-align: center;
}

.footer-copyright {
  font-size: 14px;
  color: #cbd5e0;
}

.footer-copyright strong {
  color: #ffffff;
  font-weight: 600;
}

.footer-made-with {
  font-size: 13px;
  color: #a0aec0;
  display: flex;
  align-items: center;
  gap: 6px;
}

.heart-icon {
  color: #f56565;
  font-size: 14px;
  animation: heartBeat 1.5s infinite;
}

@keyframes heartBeat {
  0%, 100% { transform: scale(1); }
  25% { transform: scale(1.2); }
  50% { transform: scale(1); }
}

/* Floating Particles */
.footer-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 0;
  pointer-events: none;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  animation: particleFloat 20s infinite ease-in-out;
}

.particle-1 { top: 10%; left: 10%; animation-delay: 0s; }
.particle-2 { top: 20%; left: 80%; animation-delay: 2s; }
.particle-3 { top: 60%; left: 20%; animation-delay: 4s; }
.particle-4 { top: 80%; left: 70%; animation-delay: 6s; }
.particle-5 { top: 40%; left: 50%; animation-delay: 8s; }
.particle-6 { top: 30%; left: 90%; animation-delay: 3s; }
.particle-7 { top: 70%; left: 15%; animation-delay: 5s; }
.particle-8 { top: 50%; left: 85%; animation-delay: 7s; }

@keyframes particleFloat {
  0%, 100% {
    transform: translateY(0) scale(1);
    opacity: 0.6;
  }
  50% {
    transform: translateY(-50px) scale(1.5);
    opacity: 1;
  }
}

/* ==================== RESPONSIVE DESIGN ==================== */

@media (max-width: 1200px) {
  .footer-top {
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }
  
  .footer-brand-col {
    grid-column: 1 / -1;
  }
}

@media (max-width: 768px) {
  .modern-footer {
    padding: 60px 0 20px;
  }

  .footer-container {
    padding: 0 20px;
  }

  .footer-top {
    grid-template-columns: 1fr;
    gap: 40px;
  }

  .footer-social-grid {
    grid-template-columns: repeat(3, 1fr);
  }

  .footer-bottom {
    flex-direction: column;
    gap: 20px;
    text-align: center;
  }

  .footer-bottom-center {
    align-items: center;
  }

  .footer-logo {
    width: 60px;
    height: 60px;
  }

  .footer-brand-name {
    font-size: 24px;
  }
}

@media (max-width: 480px) {
  .footer-social-grid {
    grid-template-columns: 1fr;
  }

  .footer-cta-col {
    padding: 20px;
  }
}